.contact-container {
  /* height: 70vh; */
  color: #d25b43;
  margin: 1%;
  padding-bottom: 5%;
}

.icons-container {
  text-align: right;
  width: 60%;
  padding: 1%;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 5px;
}

.icons {
  padding: 4%;
  display: flex;
  justify-content: space-evenly;
}

i {
  font-size: 8vw;
  color: white;
}

i:hover {
  color: #2d3447;
}

.landing-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
}
.perspective-text {
  color: white;
  font-family: Arial;
  font-size: 3.2rem;
  font-weight: 900;
  letter-spacing: -1px;
  text-transform: uppercase;
  text-align: center;
  align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  margin: 0 auto;
}

.perspective-line {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100px;
  width: 100%;
  justify-content: center;
  flex-wrap: wrap;
}

p {
  margin: 0;
  line-height: 50px;
  transition: all 0.5s ease-in-out;
}

.perspective-line:nth-child(1) {
  background: #2d3447;
  left: 29px;
}

.perspective-line:nth-child(2) {
  left: 58px;
  background: #f07e6e;
}

.perspective-line:nth-child(3) {
  left: 87px;
  background: #2d3447;
}

.perspective-line:nth-child(4) {
  left: 116px;
  background: #f07e6e;
  display: flex;
  flex-wrap: wrap;
}

.perspective-line:nth-child(5) {
  left: 145px;
}

.landing-navs {
  width: 100%;
  justify-content: center;
  text-align: center;
}

.btn {
  color: white;
  background: #2d3447;
  padding: 10px 20px;
  font-size: 12px;
  text-decoration: none;
  letter-spacing: 2px;
  text-transform: uppercase;
  width: 200px;
  margin: 3%;
}

.btn:hover {
  border: none;
  background: rgba(0, 0, 0, 0.4);
  background: #fff;
  padding: 20px 20px #000;
  color: #1b1b1b;
}

.footer {
  color: white;
  background: black;
  text-align: center;
}

